.move-up {
    animation: MoveUp .1s ease-in-out;
}

@keyframes MoveUp {
    0% { transform: translateY(200px); }
    100% { transform: translateY(0); }
}

* {
    margin: 0 auto;
    -webkit-user-select: none;
}
body {
    animation: bodycolor 16s infinite;
}
.container {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    left: 0;
}
.spaceship {
    position: absolute;
    top: 30%;
    height: 250px;
    width: 300px;
    margin-left: -150px;
    animation: spaceshipleft 8s infinite;
}
.spaceship_top {
    margin: auto;
    position: absolute;
    bottom: 0;
    left: 0;
    top: 0;
    right: 0;
    top: -150px;
    height: 150px;
    width: 200px;
    background-color: #f6a7ba;
    border-radius: 8rem 8rem 0 0;
    box-shadow: inset 6px 2px 3px 0 #c2c2c2;
}
.spaceship_top::before {
    content: "";
    position: absolute;
    left: 150px;
    top: 32px;
    height: 8px;
    width: 10px;
    background-color: white;
    border-radius: 2rem;
    transform: rotate(42deg);
}
.spaceship_top::after {
    content: "";
    position: absolute;
    left: 130px;
    top: 20px;
    height: 10px;
    width: 20px;
    background-color: white;
    border-radius: 2rem;
    transform: rotate(32deg);
}
.spaceship_middle {
    margin: auto;
    position: absolute;
    bottom: 0;
    left: 0;
    top: 0;
    right: 0;
    width: 300px;
    height: 70px;
    overflow: hidden;
    border-radius: 5rem;
    box-shadow: inset 6px 2px 3px 0 #c2c2c2;
    z-index: 3;
    animation: colorswap_middle 16s infinite;
}
.spaceship_bottom {
    margin: auto;
    position: absolute;
    bottom: 0;
    left: 0;
    top: 0;
    right: 0;
    top: 75px;
    width: 165px;
    height: 30px;
    background-color: #9c0327;
    border-radius: 0 0 2rem 2rem;
    z-index: -1;
    box-shadow: inset 5px 2px 3px 0 #c2c2c2;
    z-index: 2;
}
