@keyframes starpopup {
    0% {
        opacity: 0;
        transform: rotate(35deg) scale(0.1);
    }
    20% {
        opacity: 0;
        transform: rotate(235deg) scale(0.1);
    }
    21% {
        opacity: 1;
        transform: rotate(-135deg) scale(0.1);
    }
    28% {
        opacity: 0;
        transform: rotate(-635deg) scale(0.1);
    }
    55% {
        opacity: 0;
        transform: rotate(135deg) scale(0.1);
    }
    70% {
        opacity: 0;
        transform: rotate(935deg) scale(0.1);
    }
    71% {
        opacity: 1;
        transform: rotate(-135deg) scale(0.1);
    }
    78% {
        opacity: 0;
        transform: rotate(-635deg) scale(0.1);
    }
    100% {
        opacity: 0;
        transform: rotate(35deg) scale(0.1);
    }
}
@keyframes robotup {
    0% {
        top: 80%;
    }
    20% {
        top: 80%;
    }
    25% {
        top: 43%;
    }
    50% {
        top: 44%;
    }
    70% {
        top: 44%;
    }
    75% {
        top: 80%;
    }
    100% {
        top: 80%;
    }
}
@keyframes abduct {
    0% {
        opacity: 0;
    }
    42% {
        opacity: 0;
    }
    43% {
        opacity: 0.8;
        transform: rotateX(75deg) translateY(0px);
    }
    45% {
        transform: rotateX(75deg) translateY(200px) scale(0.8);
        opacity: 0.8;
    }
    47% {
        transform: rotateX(75deg) translateY(0px);
        opacity: 0.8;
    }
    48% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}
@keyframes abductreverse {
    0% {
        opacity: 0;
    }
    42% {
        opacity: 0;
    }
    43% {
        opacity: 0.8;
        transform: rotateX(75deg) translateY(0px);
    }
    45% {
        opacity: 0.8;
        transform: rotateX(75deg) translateY(-100px);
    }
    48% {
        opacity: 0.8;
        transform: rotateX(75deg) translateY(0px);
    }
    49% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}
@keyframes moveleft {
    0% {
        left: 350px;
    }
    100% {
        left: -500px;
    }
}
@keyframes spaceshipleft {
    0% {
        transform: rotate(-10deg);
        left: 200%;
    }
    15% {
        transform: rotate(-10deg);
    }
    30% {
        left: 45%;
        transform: rotate(10deg);
    }
    40% {
        left: 50%;
        transform: rotate(0deg) translateY(0px);
    }
    45% {
        left: 50%;
        transform: rotate(0deg) translateY(-10px);
    }
    50% {
        left: 50%;
        transform: rotate(0deg) translateY(0px);
    }
    60% {
        left: 55%;
        transform: rotate(-10deg);
    }
    61% {
        left: 55%;
    }
    100% {
        left: -100%;
    }
}
@keyframes bodycolor {
    0% {
        background-image: linear-gradient(to top,#ee3d67,#ef466e,#f04f75,#f1567b,#f25e82);
    }
    20% {
        background-image: linear-gradient(to top,#ee3d67,#ef466e,#f04f75,#f1567b,#f25e82);
    }
    21% {
        background-image: linear-gradient(to right top,#ffffff,#f7f2fa,#f4e4f1,#f3d5e4,#f4c6d2);
    }
    70% {
        background-image: linear-gradient(to right top,#ffffff,#f7f2fa,#f4e4f1,#f3d5e4,#f4c6d2);
    }
    71% {
        background-image: linear-gradient(to top,#ee3d67,#ef466e,#f04f75,#f1567b,#f25e82);
    }
    100% {
        background-image: linear-gradient(to top,#ee3d67,#ef466e,#f04f75,#f1567b,#f25e82);
    }
}
@keyframes abductcolor {
    0% {
        border: 2px solid white;
    }
    20% {
        border: 2px solid white;
    }
    21% {
        border: 2px solid #ee3d67;
    }
    70% {
        border: 2px solid #ee3d67;
    }
    71% {
        border: 2px solid white;
    }
    100% {
        border: 2px solid white;
    }
}
@keyframes colorswap {
    0% {
        background-color: #ee3d67;
    }
    20% {
        background-color: #ee3d67;
    }
    21% {
        background-color: #fff;
    }
    70% {
